.header {
  //box-sizing: content-box
  display: flex;
  justify-content: space-between;
  height: auto;
  width: 100%;
  padding: 30px 30px 0;
  position: fixed;
  z-index: 999;

  .logo {
    height: 60px; }

  .hamburger {
    background: transparent;
    color: #ccc;
    display: block;
    position: relative;
    overflow: hidden;
    margin: 0;
    padding: 0;
    width: 50px;
    height: 50px;
    font-size: 0;
    text-indent: -9999px;
    appearance: none;
    border: 1px solid #000;
    box-shadow: 0 0 0 #fff;

    border-radius: 4px;
    cursor: pointer;
    transition: all 0.5s;
    &:focus {
      outline: none; }
    & span {
      top: 21px;
      left: 6px;
      right: 6px;
      transition: background 0s 0.25s;
      &, &::before, &::after {
        background-color: #b3b3b3;
        height: 5px;
        position: absolute;
        display: block; }
      &::before, &::after {
        left: 0;
        width: 100%;
        content: "";
        transition-duration: 0.25s, 0.25s;
        transition-delay: 0.25s, 0s; }
      &::before {
        top: -14px;
        transition-property: top, transform; }
      &::after {
        bottom: -14px;
        transition-property: bottom, transform; } }
    &:hover {
      box-shadow: 0 0 4px #fff, inset 0 0 2px #fff; }
    &.animx {
      //border-color: #cccccc
      box-shadow: 0 0 4px #fff, inset 0 0 2px #fff;
      & span {
        background: none;
        &::before, &::after {
          box-shadow: 0 0 2px #fff;
          background-color: #b3b3b3;
          transition-delay: 0s, 0.25s; }
        &::before {
          top: 0;
          transform: rotate(45deg);
          background: #fff; }
        &::after {
          bottom: 0;
          transform: rotate(-45deg);
          background: #fff; } } } } }

