#root {
  max-width: 100vw;
  max-height: 100vh;
  overflow: hidden;
  perspective: 100px;
  background: #111; }

.social-icon {
  ul {
    display: flex;
    li a {
      display: block;
      width: 45px;
      height: 45px;
      line-height: 45px;
      border: 1px solid transparent;
      font-size: 1.5rem;
      transition: border-radius 0.5s ease-in-out, color 0.5s ease;
      border-radius: 0.1rem;
      text-align: center;
      //i
      //  display: block
      //  margin: auto
      &:hover {
        border-radius: 2rem;
        transition: all 0.5s ease;
        color: #ff4200;
        border-color: #ff4200; } } }
  &.right {
    position: fixed;
    top: 50%;
    right: 2%;
    ul {
      flex-direction: column;
      $size: 10;
      li {
        transform: translateX(0);
        transition: all 0.5s ease; }
      &.active li {
        transform: translateX(200%); }
      @for $i from 0 through $size {
        li:nth-child(#{$i}) {
          transition-delay: #{$i * 0.1}s; } } } }
  &.bottom {
    position: absolute;
    left: 55%;
    bottom: 2%;
    ul li {
      transform: translateY(250%);
      transition: all 0.5s ease-in-out; }

    ul.active {
      $size: 10;
      li {
        transform: translateY(0); }
      @for $i from 0 through $size {
        li:nth-child(#{$i}) {
          transition-delay: #{$i * 0.1}s; } } } } }


.block-counter {
  position: fixed;
  bottom: 50%;
  transform: translateY(100%);
  left: 30px;
  z-index: 22;
  ul {
    //perspective: 100px
    li {
      padding: 0.5rem 0;
      font: {
        family: ''; }
      height: 1rem;
      $size: 4px;
      transition: height 1s ease, all 1s ease;
      transform-origin: left top;
      //transform: rotateX(90deg) translateY(-4rem)
      .number {
        color: #737373;
        line-height: 4rem;
        font: {
          family: 'Avenir-Black', sans-serif;
          size: 4rem; }
        transition: all 1s ease;
        height: 0;
        overflow: hidden;
        //opacity: 0
        margin-left: -20px; }
      .line {
        width: $size;
        height: 4rem;
        background: #737373;
        transition: all 1s ease;
        opacity: 0; }
      .block-counter__wrap {
        display: flex;
        span {
          color: #ffffff;
          padding-left: 1rem;
          font: {
            family: 'Avenir-Black', sans-serif;
            size: 1rem; }
          line-height: 2rem;
          transition: all 1s ease;
          opacity: 0; }
        .point {
          margin: calc(1rem - #{$size}) 0;
          //margin-left: $size / -0.5
          width: $size * 2;
          height: $size * 2;
          border-radius: $size;
          background: #737373;
          transition: all 1s ease; } }
      //transform: rotateX(90deg)
      &.active {
        //transform: rotateX(0deg) translateY(0)
        height: calc(11rem);
        .line {
          background-color: #ffffff;
          opacity: 1; }
        .number {
          color: #ffffff;
          opacity: 1;
          height: 4rem; }
        .block-counter__wrap {
          .point {
            background-color: #ffffff; }
          span {
            opacity: 1; } } } } } }




