nav.menu {
  position: absolute;
  z-index: 0;
  height: 80%;
  right: 30px;
  top: 10%;
  ul {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    li {
      display: block;
      line-height: 4rem;
      text-align: right;
      a {
        text-decoration: none;
        cursor: pointer;
        font-family: Roboto, sans-serif;
        font-size: 2rem;
        position: relative;
        transition: text-shadow 0.5s ease, color 0.3s ease-in-out;
        transition-delay: 0s, 0.2s;
        color: #111;
        &:before {
          content: '';
          display: block;
          position: absolute;
          height: 8px;
          top: 50%;
          left: 0;
          margin-top: -4px;
          width: 100%;
          background: #ff4200;
          opacity: 0;
          transition: 0.3s ease-in-out;
          transition-delay: 0.2s, 0s;
          cursor: default; }
        //&.active:before
        //
        //  opacity: 1
        &:not(.active) {
          &:before {
            content: none; }
          &:hover {
            text-shadow: 2px 2px 6px #ff4200 {

 } } } } } } }              //opacity: 0.3



