* {
  box-sizing: border-box; }
ul {
  list-style: none;
  padding: 0;
  margin: 0; }
a {
  text-decoration: none;
  color: #ffffff; }

.app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  transition: all 0.5s ease;
  transform-origin: left center;
  transform: translateX(0) translateZ(0px) rotateY(0deg);
  position: relative;
  z-index: 2;
  .page {
    transition: .5s ease-out, width 0.5s ease;
    overflow-y: auto;
    overflow-x: hidden;
    height: 100vh;
    transition: {}
    &::-webkit-scrollbar {
      width: 8px;
      //background-color: #F5F5F5
      background: none;
      position: absolute;
      transition: width 0.5s ease; }
    &::-webkit-scrollbar-thumb {
      border-radius: 6px;
      -webkit-box-shadow: inset 0 0 3px #111;
      background-color: rgba(#ff4200,0.8); }

    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 5px #111;
      //border-radius: 6px
      background-color: #888;
 } }      //background: none

  &.menu {
    transform: translateX(-100px) translateZ(-30px) rotateY(2.5deg);
    //box-shadow: 3px 3px 3px #ff4200, 4px 4px 4px #ff4200, 5px 5px 5px #ff4200, 6px 6px 6px #ff4200
    //&:hover
    //  box-shadow: 5px 5px 17px #ff4200, 3px 3px 3px #ff4200, -2px -2px 3px #ff4200,4px 4px 4px #ff4200, 5px 5px 5px #ff4200, 6px 6px 6px #ff4200
    box-shadow: -3px -3px 3px #ff4200, -4px -4px 4px #ff4200, -5px -5px 5px #ff4200;
    &:hover {
      box-shadow: -3px -3px 3px #ff4200, 2px 2px 37px #ff4200, 2px 2px 3px #ff4200;
      transform: translateX(-100px) translateZ(-25px) rotateY(1.8deg); }
    //.menu
    //  z-index: 1
    & + .menu ul li a {
      color: #ffffff;
      &.active:before {
        opacity: 1; }
      //transition: text-shadow 0.3s ease
      .page::-webkit-scrollbar {
        transition: width 0.5s ease;
        width: 0; } } } }





section, footer {
  width: 100%;
  height: 100vh;
  &:nth-child(2) {
    background: red; }

  &:nth-child(3) {
    background: blueviolet; }
  &:nth-child(4) {
    background: forestgreen; }
  &:nth-child(5) {
    background: pink; }
  &:nth-child(6) {
    background: aqua; } }

footer {
  background: #ff4200;
  height: 20vh; }
